.checkmark svg {
  width: 30vw;
}
.checkmark-container {
  display: grid;
  grid-template-rows: 50vw auto;
  margin-top: auto;
  margin-bottom: auto;
  height: 100%;
  // margin-top: 10em;
}
.checkmark {
  margin-top: 25vh;
  margin-left: auto;
  margin-right: auto;
  vertical-align: bottom;
  // position: absolute;
  // position: relative;
  // left: 35vw;
  top: 15vh;
  // width: 420px;
  // padding-top: 40px;
}

.path {
  stroke-dasharray: 1000;
  stroke-dashoffset: 0;
  animation: dash 2s ease-in-out;
  -webkit-animation: dash 2s ease-in-out;
}

.spin {
  animation: spin 2s;
  -webkit-animation: spin 2s;
  transform-origin: 50% 50%;
  -webkit-transform-origin: 50% 50%;
}

#complete_message {
  font-family: sans-serif;
  letter-spacing: 0.1em;
  color: #FFBA49;
  font-size: 3em;
  margin-left: auto;
  margin-right: auto;
  // margin: 20px auto;
  // margin-left: -2em;
  text-align: center;
  animation: text .5s linear .4s;
}

@keyframes dash {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes text {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}